"use client"
import { motion } from "framer-motion"
import { useState, useEffect } from "react"
import { ArrowRight, Sparkles, Calendar, Zap, CheckCircle, Twitter, Mail, Moon, Sun } from "lucide-react"

export default function MarketeerLanding() {
  const [email, setEmail] = useState("")
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [isDarkMode, setIsDarkMode] = useState(true)

  useEffect(() => {
    document.documentElement.classList.toggle("dark", isDarkMode)
  }, [isDarkMode])

  const toggleDarkMode = () => {
    setIsDarkMode(prev => !prev)
  }


  // Party poppers effect on page load
  useEffect(() => {
    const createConfetti = () => {
      const colors = isDarkMode 
        ? ['#60A5FA', '#A78BFA', '#F472B6', '#FBBF24', '#F87171', '#34D399']
        : ['#3B82F6', '#8B5CF6', '#EC4899', '#F59E0B', '#EF4444', '#10B981']
      const confettiCount = 50
      
      for (let i = 0; i < confettiCount; i++) {
        const confetti = document.createElement('div')
        confetti.style.position = 'fixed'
        confetti.style.left = Math.random() * 100 + 'vw'
        confetti.style.top = '-10px'
        confetti.style.width = Math.random() * 8 + 4 + 'px'
        confetti.style.height = confetti.style.width
        confetti.style.backgroundColor = colors[Math.floor(Math.random() * colors.length)]
        confetti.style.borderRadius = Math.random() > 0.5 ? '50%' : '2px'
        confetti.style.pointerEvents = 'none'
        confetti.style.zIndex = '9999'
        confetti.style.animation = `confetti-fall ${Math.random() * 3 + 2}s linear forwards`
        
        document.body.appendChild(confetti)
        
        setTimeout(() => {
          if (confetti.parentNode) {
            confetti.parentNode.removeChild(confetti)
          }
        }, 5000)
      }
    }

    // Add confetti CSS animation
    const style = document.createElement('style')
    style.textContent = `
      @keyframes confetti-fall {
        0% {
          transform: translateY(-100vh) rotate(0deg);
          opacity: 1;
        }
        100% {
          transform: translateY(100vh) rotate(720deg);
          opacity: 0;
        }
      }
    `
    document.head.appendChild(style)

    // Trigger confetti after a short delay
    const timer = setTimeout(createConfetti, 1000)
    
    return () => {
      clearTimeout(timer)
      if (style.parentNode) {
        style.parentNode.removeChild(style)
      }
    }
  }, [isDarkMode])

  const handleWaitlistSubmit = async (e: any) => {
    e.preventDefault()
    setIsLoading(true)
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000))
      setIsSubmitted(true)
      setEmail("")
      
      // Trigger celebration confetti
      const celebrateConfetti = () => {
        const colors = isDarkMode
          ? ['#34D399', '#10B981', '#6EE7B7', '#A7F3D0']
          : ['#10B981', '#059669', '#34D399', '#6EE7B7']
        for (let i = 0; i < 30; i++) {
          const confetti = document.createElement('div')
          confetti.style.position = 'fixed'
          confetti.style.left = Math.random() * 100 + 'vw'
          confetti.style.top = '-10px'
          confetti.style.width = Math.random() * 6 + 3 + 'px'
          confetti.style.height = confetti.style.width
          confetti.style.backgroundColor = colors[Math.floor(Math.random() * colors.length)]
          confetti.style.borderRadius = '50%'
          confetti.style.pointerEvents = 'none'
          confetti.style.zIndex = '9999'
          confetti.style.animation = `confetti-fall ${Math.random() * 2 + 1.5}s linear forwards`
          
          document.body.appendChild(confetti)
          
          setTimeout(() => {
            if (confetti.parentNode) {
              confetti.parentNode.removeChild(confetti)
            }
          }, 3500)
        }
      }
      
      celebrateConfetti()
    } catch (error) {
      console.error('Error:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.15,
        duration: 0.6
      }
    }
  }

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        ease: [0.6, -0.05, 0.01, 0.99]
      }
    }
  }

  const features = [
    {
      icon: Twitter,
      title: "AI learns your voice",
      description: "Analyzes your existing tweets to match your unique tone and style"
    },
    {
      icon: Sparkles,
      title: "Daily tweet ideas",
      description: "Fresh content generated daily based on your SaaS product and audience"
    },
    {
      icon: Calendar,
      title: "Smart scheduling",
      description: "Post immediately or schedule tweets for optimal engagement times"
    }
  ]

  const steps = [
    { step: "01", title: "Connect X Account", desc: "Sign in with Twitter OAuth" },
    { step: "02", title: "Describe Your SaaS", desc: "Tell us about your product & audience" },
    { step: "03", title: "AI Learns Your Voice", desc: "We analyze your existing tweets" },
    { step: "04", title: "Get Daily Tweets", desc: "Review, edit, and post or schedule" }
  ]

  return (
    <div className="min-h-screen bg-white dark:bg-gray-900 overflow-hidden transition-colors duration-300">
      {/* Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <motion.div
          className="absolute top-20 left-10 w-96 h-96 bg-gradient-to-r from-blue-100/40 to-purple-100/40 dark:from-blue-900/20 dark:to-purple-900/20 rounded-full blur-3xl"
          animate={{
            scale: [1, 1.2, 1],
            opacity: [0.3, 0.5, 0.3],
          }}
          transition={{
            duration: 8,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div
          className="absolute bottom-20 right-10 w-80 h-80 bg-gradient-to-r from-pink-100/30 to-yellow-100/30 dark:from-pink-900/20 dark:to-yellow-900/20 rounded-full blur-3xl"
          animate={{
            scale: [1.2, 1, 1.2],
            opacity: [0.2, 0.4, 0.2],
          }}
          transition={{
            duration: 10,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 2
          }}
        />
      </div>

      <div className="relative z-10">
        {/* Header */}
        <motion.header
          className="px-6 py-6"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          <div className="max-w-7xl mx-auto flex justify-between items-center">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-gradient-to-br from-blue-600 to-purple-600 rounded-xl flex items-center justify-center shadow-lg">
                <Sparkles className="w-6 h-6 text-white" />
              </div>
              <span className="text-2xl font-bold bg-gradient-to-r from-gray-900 to-gray-700 dark:from-gray-100 dark:to-gray-300 bg-clip-text text-transparent">
                Marketeer
              </span>
            </div>
            
            <div className="flex items-center gap-4">
              {/* Dark Mode Toggle */}
              <motion.button
                onClick={toggleDarkMode}
                className="p-2.5 rounded-xl bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm border border-gray-200/60 dark:border-gray-700/60 shadow-sm hover:shadow-md transition-all duration-200 group"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <motion.div
                  initial={false}
                  animate={{ rotate: isDarkMode ? 180 : 0 }}
                  transition={{ duration: 0.3 }}
                >
                  {isDarkMode ? (
                    <Sun className="w-5 h-5 text-yellow-500 group-hover:text-yellow-400" />
                  ) : (
                    <Moon className="w-5 h-5 text-gray-700 group-hover:text-gray-600" />
                  )}
                </motion.div>
              </motion.button>
              
              <motion.div
                className="text-sm text-gray-700 dark:text-gray-300 px-4 py-2 bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm rounded-full border border-gray-200/60 dark:border-gray-700/60 shadow-sm font-medium"
                whileHover={{ scale: 1.05 }}
                transition={{ duration: 0.2 }}
              >
                Coming Soon
              </motion.div>
            </div>
          </div>
        </motion.header>

        {/* Hero Section */}
        <motion.section
          className="px-6 py-12"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          <div className="max-w-4xl mx-auto text-center">
            {/* Badge */}
            <motion.div variants={itemVariants} className="inline-block mb-6">
              <div className="inline-flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-blue-50 to-purple-50 dark:from-blue-900/30 dark:to-purple-900/30 border border-blue-200/50 dark:border-blue-700/50 rounded-full shadow-sm hover:shadow-md transition-shadow">
                <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
                <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">AI-Powered Twitter Marketing</span>
              </div>
            </motion.div>

            {/* Main Headline */}
            <motion.h1 
              variants={itemVariants}
              className="text-4xl lg:text-6xl font-bold leading-tight mb-6"
            >
              <span className="bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-gray-100 dark:via-gray-200 dark:to-gray-100 bg-clip-text text-transparent">
                Never Run Out of
              </span>
              <br />
              <span className="bg-gradient-to-r from-blue-600 via-blue-700 to-purple-600 bg-clip-text text-transparent">
                Tweet Ideas
              </span>
              <span className="text-gray-900 dark:text-gray-100"> Again</span>
            </motion.h1>

            {/* Subtitle */}
            <motion.p 
              variants={itemVariants}
              className="text-lg lg:text-xl text-gray-600 dark:text-gray-400 mb-8 max-w-3xl mx-auto leading-relaxed"
            >
              Marketeer learns your voice from existing tweets and generates daily content 
              tailored to your SaaS product. <span className="font-semibold text-gray-900 dark:text-gray-100">Post consistently, grow your audience.</span>
            </motion.p>

            {/* Waitlist Form */}
            <motion.div variants={itemVariants} className="mb-12">
              {!isSubmitted ? (
                <div className="flex flex-col sm:flex-row gap-3 max-w-lg mx-auto">
                  <div className="flex-1">
                    <input
                      type="email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      placeholder="Enter your email address"
                      required
                      className="w-full px-5 py-3.5 rounded-xl border-2 border-gray-200 dark:border-gray-700 focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10 outline-none text-gray-800 dark:text-gray-200 transition-all bg-white/90 dark:bg-gray-800/90 backdrop-blur-sm placeholder-gray-500 dark:placeholder-gray-400 font-medium shadow-sm hover:shadow-md"
                    />
                  </div>
                  <motion.button
                    type="submit"
                    disabled={isLoading}
                    onClick={handleWaitlistSubmit}
                    className="px-7 py-3.5 bg-gradient-to-r from-blue-600 via-blue-700 to-purple-600 hover:from-blue-700 hover:via-blue-800 hover:to-purple-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl active:shadow-md transition-all duration-200 text-base group disabled:opacity-50 disabled:cursor-not-allowed min-w-[140px] flex items-center justify-center"
                    whileHover={{ scale: 1.02, y: -1 }}
                    whileTap={{ scale: 0.98 }}
                  >
                    {isLoading ? (
                      <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                    ) : (
                      <>
                        Join Waitlist
                        <ArrowRight className="ml-2 w-4 h-4 group-hover:translate-x-0.5 transition-transform" />
                      </>
                    )}
                  </motion.button>
                </div>
              ) : (
                <motion.div
                  initial={{ scale: 0.8, opacity: 0 }}
                  animate={{ scale: 1, opacity: 1 }}
                  className="flex items-center justify-center gap-3 px-6 py-4 bg-gradient-to-r from-green-50 to-emerald-50 dark:from-green-900/30 dark:to-emerald-900/30 border-2 border-green-200 dark:border-green-700 rounded-xl max-w-md mx-auto shadow-sm"
                >
                  <CheckCircle className="w-6 h-6 text-green-600 dark:text-green-400" />
                  <span className="text-green-800 dark:text-green-300 font-semibold">You're on the waitlist! üéâ</span>
                </motion.div>
              )}

              <motion.p 
                variants={itemVariants}
                className="text-sm text-gray-500 dark:text-gray-400 mt-3 font-medium"
              >
                Join 200+ founders already on the waitlist ‚Ä¢ No spam, ever
              </motion.p>
            </motion.div>

            {/* Features Grid */}
            <motion.div 
              variants={itemVariants}
              className="grid md:grid-cols-3 gap-6 max-w-4xl mx-auto"
            >
              {features.map((feature, index) => (
                <motion.div
                  key={index}
                  className="p-6 bg-white/70 dark:bg-gray-800/70 backdrop-blur-sm rounded-xl border border-gray-200/60 dark:border-gray-700/60 hover:shadow-lg hover:border-gray-300/60 dark:hover:border-gray-600/60 transition-all duration-300 group"
                  whileHover={{ y: -3 }}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.6 + index * 0.1 }}
                >
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-100 to-purple-100 dark:from-blue-900/50 dark:to-purple-900/50 rounded-xl flex items-center justify-center mb-4 mx-auto group-hover:scale-110 transition-transform shadow-sm">
                    <feature.icon className="w-6 h-6 text-blue-600 dark:text-blue-400" />
                  </div>
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100 mb-2">{feature.title}</h3>
                  <p className="text-gray-600 dark:text-gray-400 text-sm leading-relaxed">{feature.description}</p>
                </motion.div>
              ))}
            </motion.div>
          </div>
        </motion.section>

        {/* How It Works */}
        <motion.section 
          className="px-6 py-16 bg-gradient-to-br from-gray-50/50 to-blue-50/30 dark:from-gray-800/50 dark:to-blue-900/30 backdrop-blur-sm"
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
        >
          <div className="max-w-5xl mx-auto">
            <motion.div
              className="text-center mb-12"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
            >
              <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 dark:text-gray-100 mb-4">
                How It Works
              </h2>
              <p className="text-lg text-gray-600 dark:text-gray-400">
                Get started in minutes, not hours
              </p>
            </motion.div>

            <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
              {steps.map((step, index) => (
                <motion.div
                  key={index}
                  className="text-center relative"
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  viewport={{ once: true }}
                  transition={{ delay: index * 0.1 }}
                >
                  <div className="relative">
                    <div className="w-14 h-14 bg-gradient-to-br from-blue-500 to-purple-500 rounded-xl flex items-center justify-center text-white font-bold text-base mx-auto mb-4 shadow-lg hover:scale-105 transition-transform">
                      {step.step}
                    </div>
                    {index < steps.length - 1 && (
                      <div className="hidden lg:block absolute top-7 left-full w-full h-0.5 bg-gradient-to-r from-blue-200 to-purple-200 dark:from-blue-700 dark:to-purple-700 -translate-y-1/2" />
                    )}
                  </div>
                  <h3 className="text-base font-semibold text-gray-900 dark:text-gray-100 mb-2">{step.title}</h3>
                  <p className="text-sm text-gray-600 dark:text-gray-400 leading-relaxed">{step.desc}</p>
                </motion.div>
              ))}
            </div>
          </div>
        </motion.section>

        {/* Final CTA */}
        <motion.section 
          className="px-6 py-16"
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
        >
          <div className="max-w-3xl mx-auto text-center">
            <motion.h2 
              className="text-3xl lg:text-4xl font-bold text-gray-900 dark:text-gray-100 mb-4"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
            >
              Ready to grow your SaaS on ùïè?
            </motion.h2>
            <motion.p 
              className="text-lg text-gray-600 dark:text-gray-400 mb-8"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ delay: 0.1 }}
            >
              Be among the first to experience AI-powered Twitter marketing
            </motion.p>
            
            {!isSubmitted && (
              <motion.form 
                onSubmit={handleWaitlistSubmit} 
                className="flex flex-col sm:flex-row gap-3 max-w-lg mx-auto"
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ delay: 0.2 }}
              >
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Enter your email address"
                  required
                  className="flex-1 px-5 py-3.5 rounded-xl border-2 border-gray-200 dark:border-gray-700 focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10 outline-none text-gray-800 dark:text-gray-200 transition-all bg-white/90 dark:bg-gray-800/90 backdrop-blur-sm placeholder-gray-500 dark:placeholder-gray-400 font-medium shadow-sm hover:shadow-md"
                />
                <motion.button
                  type="submit"
                  disabled={isLoading}
                  onClick={handleWaitlistSubmit}
                  className="px-7 py-3.5 bg-gradient-to-r from-blue-600 via-blue-700 to-purple-600 hover:from-blue-700 hover:via-blue-800 hover:to-purple-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl active:shadow-md transition-all duration-200 text-base group disabled:opacity-50 disabled:cursor-not-allowed min-w-[140px] flex items-center justify-center"
                  whileHover={{ scale: 1.02, y: -1 }}
                  whileTap={{ scale: 0.98 }}
                >
                  {isLoading ? (
                    <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                  ) : (
                    <>
                      Join Waitlist
                      <ArrowRight className="ml-2 w-4 h-4 group-hover:translate-x-0.5 transition-transform" />
                    </>
                  )}
                </motion.button>
              </motion.form>
            )}
          </div>
        </motion.section>

        {/* Footer */}
        <motion.footer 
          className="px-6 py-10 border-t border-gray-200/60 dark:border-gray-700/60 bg-white/50 dark:bg-gray-800/50 backdrop-blur-sm"
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
        >
          <div className="max-w-7xl mx-auto">
            <div className="flex flex-col md:flex-row justify-between items-center gap-6">
              <div className="flex items-center gap-3">
                <div className="w-8 h-8 bg-gradient-to-br from-blue-600 to-purple-600 rounded-lg flex items-center justify-center shadow-md">
                  <Sparkles className="w-5 h-5 text-white" />
                </div>
                <span className="text-xl font-bold text-gray-900 dark:text-gray-100">Marketeer</span>
              </div>
              
              <div className="flex items-center gap-6 text-sm text-gray-600 dark:text-gray-400">
                <span className="font-medium">Built for SaaS founders</span>
                <div className="flex items-center gap-2">
                  <Mail className="w-4 h-4" />
                  <span className="font-medium">hello@marketeer.ai</span>
                </div>
              </div>
            </div>
            
            <div className="mt-6 pt-6 border-t border-gray-200/50 dark:border-gray-700/50 text-center text-sm text-gray-500 dark:text-gray-400">
              ¬© 2025 Marketeer. All rights reserved.
            </div>
          </div>
        </motion.footer>
      </div>
    </div>
  )
}